// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
/* import Vue from 'vue'
import App from './App'
import router from './router' */

Vue.config.productionTip = false

// eslint-disable-next-line
/* eslint-disable no-new */

var app = new Vue({
  el: '#app',
  data: {
    name: null,
    background: null,
    jobRoles: [
      { name: 'CustomeSuccess', id: 1 },
      { name: 'FullstackEngineer', id: 2 },
      { name: 'ProductManager', id: 3 }
    ],
    primaryBusiness: [
      { name: 'Accounting', id: 1 },
      { name: 'SoftwareDevelopment', id: 2 },
      { name: 'Landscaping', id: 3 }
    ],
    selectedTechnicalBackground: null,
    selectedRole: null,
    selectedPrimaryBusiness: null,
    errors: [],
    postJob: []
  },
  methods: {
    changeJobRole (event) {
      if (event.target.options.selectedIndex > -1) {
        document.getElementById('technicalBackground').style.display = 'none'
        this.selectedRole = event.target.options[event.target.options.selectedIndex].text
        if (this.selectedRole === 'FullstackEngineer') {
          document.getElementById('technicalBackground').style.display = 'block'
        }
      }
    },

    changePrimaryBusiness (event) {
      if (event.target.options.selectedIndex > -1) {
        document.getElementById('technicalBackground').style.display = 'none'
        this.selectedPrimaryBusiness = event.target.options[event.target.options.selectedIndex].text
        if ((this.selectedRole === 'ProductManager' && this.selectedPrimaryBusiness === 'SoftwareDevelopment') || (this.selectedRole === 'FullstackEngineer')) {
          document.getElementById('technicalBackground').style.display = 'block'
        }
      }
    },

    changeTechnicalBackgroundRequried (event) {
      this.selectedTechnicalBackground = event.target.value
      event.preventDefault()
    },

    submitForm (e) {
      this.postJob = []
      this.errors = []
      if (this.name && this.selectedRole && this.selectedPrimaryBusiness) {
        return true
      }
      this.postJob.push({
        name: this.name,
        job_role: this.selectedRole,
        primary_business: this.selectedPrimaryBusiness,
        technical_background: this.selectedTechnicalBackground
      })
      if (!this.name) {
        this.errors.push('Business Name is required.')
      }
      if (!this.selectedRole) {
        this.errors.push('Job Role is required.')
      }
      if (!this.selectedPrimaryBusiness) {
        this.errors.push('Primary Business is required.')
      }
      e.preventDefault()
    }
  }
})



